.Services {
    width: 100%;
}

.top__container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.top__container .back__text {
    font-size: 5rem;
    color: rgba(105, 105, 107, 0.918);
    opacity: 0.1;
    letter-spacing: 3px;
    font-weight: 800;
    filter: blur(1.1px);
}

.top__container .top__text {
    font-family: 'Solway', serif;
    margin-top: 2.5rem;
    font-size: 1.9rem;
    color: rgb(5, 43, 214);
    position: absolute;
}

.content__service {
    justify-content: center;
    display: flex;
    margin-top: 0.7rem;
}

.services__main .card__service .icons__ {
    font-size: large;
    opacity: 0.7;
    color: rgb(17, 85, 211);
}

.services__main {
    display: flex;
    /* box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    border-radius: 10px; */
}

.services__main .card__service {
    padding: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    opacity: 0.75;
    transition: all .5s ease-in-out;
}

.card__service h4 {
    color: rgb(29, 31, 34);
    font-size: 1.1rem;
}

.services__main .card__service:hover {
    background: rgb(5, 66, 180);
    border-radius: 10px;
    transform: scale(1.1);
}

.services__main .card__service:hover>h4,
.services__main .card__service:hover>.icons__ {
    color: rgba(225, 225, 233, 0.973);
    opacity: 1;
}

@media only screen and (max-width: 600px) {
    .top__container {
        margin: 1rem 0 0 0;
    }

    .top__container .back__text {
        font-size: 3.2rem;
    }

    .top__container .top__text {
        font-size: 1.3rem;
        margin-top: 1.5rem;
    }

    .services__main .card__service h4 {
        font-size: 0.7rem;
    }
}