@import url(https://fonts.googleapis.com/css2?family=Quattrocento:wght@400;700&family=Solway&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Exo&family=Poppins:wght@400;600&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(255, 255, 255);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-thumb {
  background: rgba(168, 160, 160, 0.466);
}

::-webkit-scrollbar-thumb:hover {
  background: #2c3338;
}
.App {
  overflow-x: hidden;
}
.navbar__top {
    display: flex;
    width: 100%;
    position: fixed;
    background: rgb(255, 255, 255);
    padding: 0.6rem 0;
    align-items: center;
    justify-content: space-between;
    z-index: 999;
    height: 2.3rem;
}

.logo__container img {
    width: 8.2rem;
    margin: 0 1.5rem;
    object-fit: cover;
}

.nav__links {
    transition: all .5s ease-in-out;
    display: flex;
    margin-right: 2.2rem;
}

.nav__links .nav__items {
    margin: 0 1.9rem;
    text-decoration: none;
    transition: 0.5s;
    font-size: 1.01rem;
}

.nav__links .nav__items:hover,
.active {
    font-weight: 700;
    color: #ff4b8d;
}

.menu__toggle {
    cursor: pointer;
    margin-right: 1.8rem;
    color: rgba(2, 2, 2, 0.966);
}

.menu__toggle {
    display: none;
}

@media screen and (max-width: 800px) {
    .nav__links {
        z-index: 999;
        background: rgb(255, 255, 255);
        position: fixed;
        top: 3.5rem;
        width: 70%;
        left: -100%;
        height: 100%;
        padding: 1rem 0;
        flex-direction: column;
        color: rgb(26, 24, 24);
    }

    .nav__links .nav__items {
        margin: 1.3rem 2rem;
    }

    .menu__toggle {
        display: block;
    }
}

.show {
    left: 0;
}

.hamburger .line {
    width: 25px;
    height: 2px;
    background-color: #272a2b;
    display: block;
    margin: 5px auto;
    transition: all 0.3s ease-in-out;
}

.hamburger:hover {
    cursor: pointer;
}

#hamburger-11.is-active .line:nth-child(1),
#hamburger-11.is-active .line:nth-child(2),
#hamburger-11.is-active .line:nth-child(3) {
    transition-delay: 0.2s;
}

#hamburger-11.is-active .line:nth-child(2) {
    opacity: 0;
}

#hamburger-11.is-active .line:nth-child(1) {
    transform: translateY(7px) rotate(45deg);
}

#hamburger-11.is-active .line:nth-child(3) {
    transform: translateY(-7px) rotate(-45deg);
}
.main__loader {
    width: 100%;
    height: 100vh;
    top: 0;
    position: fixed;
    z-index: 999999;
}

.hide__content {
    -webkit-animation: zoomAnimations 1.5s ease-in-out;
            animation: zoomAnimations 1.5s ease-in-out;
    z-index: 999999;
}

@-webkit-keyframes zoomAnimations {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}

@keyframes zoomAnimations {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}

.content__loader {
    width: 100%;
    height: 100%;
    background: rgb(255, 255, 255);
}

.loader {
    position: absolute;
    top: 50%;
    left: 50%;
    justify-content: center;
    align-items: center;
    display: flex;
    width: 150px;
    transform: translate(-50%, -50%);
}

.loader span {
    width: 10px;
    height: 80px;
    background: white;
    margin: 0 1px;
    -webkit-animation: loading 800ms ease-in-out infinite;
            animation: loading 800ms ease-in-out infinite;
}

.loader span:nth-child(1) {
    background: #0084ff;
    -webkit-animation-delay: -700ms;
            animation-delay: -700ms;
}

.loader span:nth-child(2) {
    background: #eb4924;
    -webkit-animation-delay: -600ms;
            animation-delay: -600ms;
}

.loader span:nth-child(3) {
    background: #00c300;
    -webkit-animation-delay: -500ms;
            animation-delay: -500ms;
}

.loader span:nth-child(4) {
    background: #ff0084;
    -webkit-animation-delay: -400ms;
            animation-delay: -400ms;
}

.loader span:nth-child(5) {
    background: #FFFC00;
    -webkit-animation-delay: -300ms;
            animation-delay: -300ms;
}

.loader span:nth-child(6) {
    background: #cd201f;
    -webkit-animation-delay: -200ms;
            animation-delay: -200ms;
}

@-webkit-keyframes loading {

    0%,
    60%,
    100% {
        transform: scaleY(0.01);
    }

    35% {
        transform: scale(1);
    }
}

@keyframes loading {

    0%,
    60%,
    100% {
        transform: scaleY(0.01);
    }

    35% {
        transform: scale(1);
    }
}
.Footer {
    width: 100%;
    /* margin-top: 3rem; */
    /* position: fixed; */
    bottom: 0;
    background: #2c3338;
    color: #d9d9d9;
    padding: 1rem 2rem;
}

.Footer .main__content {
    margin-bottom: 2.2rem;
    display: flex;
}

.Footer .left {
    width: 39%;
    margin-right: 10px;
    margin-bottom: 3rem;
}

.Footer .center {
    width: 15%;
}

.Footer .center__list {
    width: 15%;
}

.Footer .right {
    width: 22%;
}

.Footer .box h2 {
    margin-bottom: 10px;
}

.left .content p {
    margin-bottom: 15px;
}

.left .content .social__media {
    display: flex;
}

.left .content .social__media span {
    display: flex;
    background: #363b3f;
    width: 30px;
    padding: 5px;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    height: 30px;
    margin: 5px 10px;
    cursor: pointer;
    transition: .3s;
}

.left .content .social__media .fb:hover {
    background: #3b5998;
    transform: scale(1.1);
}

.left .content .social__media .twt:hover {
    background: #1da1f2;
    transform: scale(1.1);
}

.left .content .social__media .ig:hover {
    background: #da1eb1;
    transform: scale(1.1);
}

.center .content .items {
    display: flex;
    align-items: center;
    transition: .5s;
}

.center .content .items .icons__ {
    margin: 7px 10px 7px 0;
}

.center .content .items:hover {
    cursor: pointer;
    transform: scale(1.05);
    color: #00a0dc;
}

.center__list .content li {
    font-size: 1.05rem;
    margin: 10px 10px 7px 15px;
    transition: .3s;
}

.center__list .content li:hover {
    cursor: pointer;
    transform: scale(1.05);
    color: #00a0dc;
}

.right .content .form__group .label {
    margin: 5px 0;
}

.right .content .form__group input,
.right .content .form__group textarea {
    background: #363b3f;
    outline: none;
    border: none;
    padding: 8px 10px;
    width: 100%;
    color: #fcf9f9;
}

.right .content .form__group textarea {
    resize: none;
}

.btn__submit {
    margin-top: 10px;
    padding: 10px 22px;
    border: none;
    color: #fcf9f9;
    background: #35465d;
    cursor: pointer;
    transition: .5s;
}

.btn__submit .icon-send {
    margin-left: 10px;
    width: 15px;
    height: 15px;
    transform: rotate(-45deg);
    transition: .5s;
}

.btn__submit:hover {
    border-radius: 10px;
    transform: scale(1.1);
}

.Footer hr {
    opacity: 0.5;
    width: 95%;
}

.Footer .text__copyright {
    margin: 1.2rem 0 0.5rem 0;
    width: 95%;
    display: flex;
    justify-content: space-between;
}

.Footer .text__copyright span span {
    color: #1ed760;
    font-weight: 600;
}

@media screen and (max-width: 800px) {
    .Footer .main__content {
        /* flex-direction: column; */
        flex-wrap: wrap;
    }

    .Footer .left {
        width: 85%;
    }

    /* .Footer .left {
        width: 80%;
        margin-bottom: 1rem;
    } */
    /* .Footer .center {
        width: 50%;
        margin-bottom: 1rem;
    }

    .Footer .center__list {
        width: 50%;
    } */
    .left .content .social__media span {
        margin: 10px 20px 15px 10px;
    }

    .Footer .center,
    .Footer .center__list {
        display: none;
    }

    .Footer .right {
        width: 80%;
    }

    .Footer .text__copyright {
        width: 80%;
        flex-direction: column;
    }

    .Footer .text__copyright span {
        margin: 5px 0;
    }
}
