.carousel {
    width: 100%;
    height: 25rem;
    padding: 3.5rem 2rem 0 2rem;
    display: flex;
    overflow: hidden;
}

.slide__content {
    padding: 5rem 3rem 0 4rem;
    display: flex;
    width: 90%;
    height: 20rem;
    border-radius: 20px 0 0 20px;
    background: rgb(250, 250, 250);
}

.content__left {
    width: 50%;
}

.content__left .title__product {
    font-family: 'Solway', serif;
    letter-spacing: 5px;
    font-size: 1.4rem;
    margin-bottom: 1.5rem;
}

.content__left .price__product {
    margin-bottom: 2rem;
    display: flex;
    font-size: 1.5rem;
    /* font-family: 'Balsamiq Sans', cursive; */
    letter-spacing: -1.5px;
}

.content__left .price__product .final__price {
    color: #3bcee2;
    margin-right: 2rem;
    font-weight: 700;
}

.content__left .price__product .diskon__price {
    font-weight: 400;
    text-decoration: line-through;
}

.slide__content span {
    text-align: center;
    padding: 1rem 2rem;
    border-radius: 10px;
    background: #3bcee2;
    color: rgba(255, 255, 255, 0.925);
    font-weight: 600;
    font-size: 1.2rem;
    cursor: pointer;
}

.content__right {
    width: 50%;
}

.content__right img {
    width: 100%;
    height: 95%;
}

.splide {
    width: 100%;
}

.carousel .splide__arrow--next {
    margin-right: 2rem;
}

.carousel .splide__arrow--prev {
    left: -0.6rem;
    z-index: 9;
}

.text__footer {
    position: absolute;
    bottom: 1.5rem;
}

.text__footer p {
    font-size: 0.9rem;
    letter-spacing: 1.5px;
}

.text__footer h5 {
    font-size: 1rem;
    letter-spacing: 1px;
}

.carousel .splide__pagination__page {
    background: #03a5f0;
}

@media only screen and (max-width: 798px) {
    .content__left .title__product {
        font-size: 1rem;
    }

    .carousel {
        height: 25rem;
        padding: 2rem 0 0 0;
    }
}

@media only screen and (max-width: 620px) {

    .content__left,
    .content__right {
        width: 100%;
    }

    .content__left .title__product {
        font-size: 0.8rem;
    }

    .carousel {
        height: 30rem;
        padding: 3.5rem 0 0 0;
    }

    .slide__content {
        border-radius: 0;
        padding: 2rem 1.5rem;
        height: 30rem;
        flex-direction: column;
    }

    .text__footer {
        position: absolute;
        bottom: 4.7rem;
    }

    .carousel .splide__arrow--next {
        right: -1.5rem;
    }

    .carousel .splide__arrow--prev {
        left: -3rem;
        position: absolute;
    }

    .slide__content span {
        position: absolute;
        bottom: 4.7rem;
        right: 1rem;
    }
}

@media only screen and (min-width: 1500px) {
    .slide__content {
        width: 93%;
    }
}
.Services {
    width: 100%;
}

.top__container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.top__container .back__text {
    font-size: 5rem;
    color: rgba(105, 105, 107, 0.918);
    opacity: 0.1;
    letter-spacing: 3px;
    font-weight: 800;
    -webkit-filter: blur(1.1px);
            filter: blur(1.1px);
}

.top__container .top__text {
    font-family: 'Solway', serif;
    margin-top: 2.5rem;
    font-size: 1.9rem;
    color: rgb(5, 43, 214);
    position: absolute;
}

.content__service {
    justify-content: center;
    display: flex;
    margin-top: 0.7rem;
}

.services__main .card__service .icons__ {
    font-size: large;
    opacity: 0.7;
    color: rgb(17, 85, 211);
}

.services__main {
    display: flex;
    /* box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    border-radius: 10px; */
}

.services__main .card__service {
    padding: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    opacity: 0.75;
    transition: all .5s ease-in-out;
}

.card__service h4 {
    color: rgb(29, 31, 34);
    font-size: 1.1rem;
}

.services__main .card__service:hover {
    background: rgb(5, 66, 180);
    border-radius: 10px;
    transform: scale(1.1);
}

.services__main .card__service:hover>h4,
