@media only screen and (max-width: 600px) {
    .top__container {
        margin: 1rem 0 0 0;
    }

    .top__container .back__text {
        font-size: 3.2rem;
    }

    .top__container .top__text {
        font-size: 1.3rem;
        margin-top: 1.5rem;
    }

    .services__main .card__service h4 {
        font-size: 0.7rem;
    }
}
