.resi {
    &__container {
        padding: 6rem 2rem 4rem 2rem;
        width: 100%;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        background-image: radial-gradient(circle farthest-corner at 22.4% 21.7%, rgba(4, 189, 228, 1) 0%, rgba(2, 83, 185, 1) 100.2%);
    }

    &__title {
        width: 60%;
        font-family: 'Poppins', sans-serif;
        display: flex;
        flex-direction: column;
        margin-bottom: 3rem;
        letter-spacing: 1px;

        h1 {
            color: rgb(255, 255, 255);
            font-size: 2.2rem;
            text-align: center;
            margin-bottom: 1rem;
        }

        span {
            font-size: 1rem;
            color: rgb(243, 238, 235);
            text-align: center;
        }

    }

    &__card {
        border-radius: 10px;
        width: 65%;
        padding: 2rem 2.5rem;
        background: rgb(255, 255, 255);

        &-items {
            flex-direction: row;
            width: 100%;
            display: flex;
        }

        &-result {
            margin-top: 2rem;
            font-family: 'Poppins', sans-serif;
            // display: none;

            .result {
                &__message {
                    h1 {
                        font-weight: 400;
                        letter-spacing: 4px;
                        font-size: 1.2rem;
                    }
                }

                &__description {
                    display: flex;
                    width: 95%;
                    padding: 0.5rem 1rem;
                    justify-content: space-between;
                    align-items: center;

                    .shipper,
                    .dropper {

                        h1 {
                            letter-spacing: 2px;
                            letter-spacing: 2px;
                            opacity: 0.8;
                            font-size: 1.4rem;
                        }

                        span {
                            letter-spacing: 1px;
                            opacity: 0.7;
                            font-size: 0.85rem;
                        }
                    }

                    .courier {
                        letter-spacing: 1px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        flex-direction: column;

                        .MuiSvgIcon-root {
                            transition: .5s;
                            animation: car 0.2s ease infinite;
                            color: #0084ff;
                            font-size: 2.3rem;
                        }

                    }

                }

                &__timeline {
                    padding: 1.2rem 1rem;
                    font-family: 'Poppins', sans-serif;

                    .timeline {
                        &__body {
                            position: relative;
                            margin-left: 2rem;
                            padding: 5px 0;

                            &:after {
                                content: '';
                                width: 4px;
                                height: 100%;
                                position: absolute;
                                background: #00AFF0;
                                left: -4px;
                                top: 0;
                            }
                        }

                        &__item {
                            position: relative;

                            &:after {
                                content: '';
                                width: 15px;
                                height: 15px;
                                border-radius: 50%;
                                border: 4px solid #00aff0;
                                background: rgb(255, 255, 255);
                                left: -13px;
                                top: 0;
                                z-index: 9;
                                position: absolute;
                            }
                        }

                        &__time {
                            font-weight: 600;
                            margin-left: 1.2rem;
                        }

                        &__content {
                            letter-spacing: 1px;
                            opacity: 0.7;
                            color: rgb(54, 54, 54);
                            margin: 0.4rem 0;
                            font-weight: 400;
                            font-size: 1.2rem;
                            margin-left: 2rem;
                        }

                    }

                }

            }
        }

    }

}

@keyframes car {

    0%,
    100% {
        transform: translateX(-2px);
    }

    50% {
        transform: translateX(2px);
    }
}

.form-group {
    display: flex;
    flex-direction: column;
    margin: 0 5px;

    label {
        letter-spacing: 1px;
        font-weight: 600;
        font-family: 'Poppins', sans-serif;
    }

    .form-control {
        color: rgb(71, 71, 71);
        letter-spacing: 2px;
        font-family: 'Poppins', sans-serif;
        padding: 0.5rem 1rem;
        outline: none;
        background: none;
        border: 1px solid rgb(171, 177, 179);
    }

    .form-control:focus {
        border: 1px solid rgba(2, 83, 185, 1);
    }

    .form-control:hover {
        border: 1px solid rgba(2, 83, 185, 1);
    }

}

.col-6 {
    flex: 1;
}

button.resi {
    font-family: 'Poppins', sans-serif;
    margin: 0.8rem 0 0 0;
    padding: 0.7rem;
    outline: none;
    background: none;
    border-radius: 10px;
    transition: .5s;
    font-size: 0.99rem;
    letter-spacing: 3px;
    border: none;
    background: rgb(30, 116, 221);
    color: rgb(241, 238, 233);

    &:hover {
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
        cursor: pointer;
    }

}

@media screen and (max-width: 768px) {
    .resi {
        &__container {
            width: 97%;
            padding: 6rem 1rem 4rem 1rem;
        }

        &__title {
            width: 90%;
        }

        &__card {
            width: 80%;
        }

    }
}

@media screen and (max-width: 540px) {
    .resi {
        &__container {
            width: 97%;
            padding: 6rem 1rem 0 1rem;
        }

        &__title {
            width: 90%;

            h1 {
                font-size: 1.4rem;
            }

            span {
                font-size: 0.8rem;
            }

        }

        &__card {
            width: 100%;

            &-items {
                padding: 0 0.2rem;
                display: flex;
                width: 95%;
                flex-direction: column;
            }

            &-result {
                .result {
                    &__message {
                        h1 {
                            font-weight: 400;
                            letter-spacing: 1px;
                            font-size: 1.2rem;
                        }
                    }

                    &__description {
                        flex-direction: column;
                        align-items: flex-start;

                        .shipper,
                        .dropper {
                            margin: 1rem 0;
                        }

                    }

                    &__timeline {
                        padding: 0.8rem 0.9rem;
                        font-family: 'Poppins', sans-serif;

                        .timeline {
                            &__body {
                                margin-left: 0.5rem;
                            }

                            &__content {
                                font-size: 1.05rem;
                            }

                        }
                    }
                }
            }
        }

    }

    button.resi {
        width: 95%;
    }

    .form-group {
        label {
            margin-top: 0.5rem;
        }
    }

}