.Product {
    display: flex;
    padding: 5rem 2rem 0 2rem;
    width: 93%;
    height: 100%;
    flex-direction: column;
}

.Product .prod__top {
    display: flex;
    width: 100%;
    flex-direction: column;
    letter-spacing: 2px;
}

.Product .prod__top .sub__prod {
    font-size: 1.25rem;
    letter-spacing: 8px;
    font-family: 'Poppins', sans-serif;
}

.Product .prod__top .title__prod {
    letter-spacing: 15px;
    font-family: 'Solway', serif;
    font-size: 2.1rem;
    font-weight: 600;
}

.search {
    &__container {
        padding-top: 6px;
    }

    &__title {
        font-family: 'Solway', serif;
        margin-bottom: 5px;
        display: flex;
        font-size: 22px;
        font-weight: 900;
        color: #ff8b88;
    }

    &__input {
        width: 97%;
        padding: 12px 24px;

        background-color: transparent;
        transition: transform 250ms ease-in-out;
        font-size: 14px;
        line-height: 18px;

        color: #575756;
        background-color: transparent;
        /*         background-image: url(http://mihaeltomic.com/codepen/input-search/ic_search_black_24px.svg); */

        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='24' height='24' viewBox='0 0 24 24'%3E%3Cpath d='M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z'/%3E%3Cpath d='M0 0h24v24H0z' fill='none'/%3E%3C/svg%3E");
        background-repeat: no-repeat;
        background-size: 18px 18px;
        background-position: 95% center;
        border-radius: 50px;
        border: 1px solid #575756;
        transition: all 250ms ease-in-out;
        backface-visibility: hidden;
        transform-style: preserve-3d;

        &::placeholder {
            color: color(#575756 a(0.8));
            letter-spacing: 2.5px;
        }

        &:hover,
        &:focus {
            padding: 12px 10px;
            outline: 0;
            border: 1px solid transparent;
            border-bottom: 1px solid #575756;
            border-radius: 0;
            background-position: 100% center;
        }
    }
}

.container__product {
    margin-top: 0.5rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    overflow: hidden;
    padding: 0.5rem 0;
}

.container__product a {
    color: #000;
    text-decoration: none;
}

.product {
    &__card {
        padding: 10px;
        width: 15rem;
        margin: 15px 10px;
        background: #faf9f9;
        box-shadow: 6px 6px 12px #e9e8e8,
            -6px -6px 12px #ffffff;
        border-radius: 20px;

        &:hover {
            cursor: pointer;
        }
    }


    &__image {
        transition: .3s;
        width: 100%;
    }

    &__image>img {
        background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
        width: 100%;
        height: 14rem;
        border-radius: 20px;
        object-fit: scale-down;
    }

    &__detail {
        font-family: 'Solway', serif;
        display: flex;
        margin: 5px 0 0 10px;
        flex-direction: column;
    }

    &__name {
        font-weight: 600;
        font-size: 1.1rem;
        letter-spacing: 2px;
    }

    &__price {
        margin: 5px 0 0 0;
        font-size: 0.8rem;
        letter-spacing: 1px;
    }

    &__card:hover>&__image {
        transform: scale(1.1);
    }

}

@media screen and (max-width: 800px) {
    .Product {
        padding: 5.2rem 1rem 0 1rem;
    }

    .Product .prod__top .sub__prod {
        font-size: 1.15rem;
    }

    .Product .prod__top .title__prod {
        font-size: 1.9rem;
    }

    .search {
        &__title {
            display: flex;
            font-size: 18px;
            font-weight: 900;
            color: #ff8b88;
        }

        &__input {
            width: 80%;
        }
    }

    .container__product {
        justify-content: center;
    }

    .product {
        &__card {
            width: 11rem;
            margin: 15px 10px;
        }

        &__image>img {
            height: 12rem;
        }

        &__name {
            font-weight: 600;
            font-size: 1rem;
            letter-spacing: 2px;
        }

        &__price {
            margin: 5px 0 0 0;
            font-size: 0.7rem;
            letter-spacing: 1px;
        }
    }
}

@media screen and (max-width: 414px) {
    .container__product {
        justify-content: start;
    }

    .Product .prod__top .sub__prod {
        font-size: 1rem;
    }

    .Product .prod__top .title__prod {
        font-size: 1.6rem;
    }

    .product {
        &__card {
            width: 88%;
            margin: 15px 6px;
        }

        &__image>img {
            height: 11rem;
        }
    }

    &__name {
        font-weight: 600;
        font-size: 0.8rem;
        letter-spacing: 2px;
    }

    &__price {
        margin: 5px 0 0 0;
        font-size: 0.5rem;
        letter-spacing: 1px;
    }
}

@media screen and (max-width: 375px) {
    .container__product {
        justify-content: center;
    }

    .product {
        &__card {
            width: 88%;
            margin: 15px 0;
        }

        &__image>img {
            height: 15rem;
        }
    }

}