.navbar__top {
    display: flex;
    width: 100%;
    position: fixed;
    background: rgb(255, 255, 255);
    padding: 0.6rem 0;
    align-items: center;
    justify-content: space-between;
    z-index: 999;
    height: 2.3rem;
}

.logo__container img {
    width: 8.2rem;
    margin: 0 1.5rem;
    object-fit: cover;
}

.nav__links {
    transition: all .5s ease-in-out;
    display: flex;
    margin-right: 2.2rem;
}

.nav__links .nav__items {
    margin: 0 1.9rem;
    text-decoration: none;
    transition: 0.5s;
    font-size: 1.01rem;
}

.nav__links .nav__items:hover,
.active {
    font-weight: 700;
    color: #ff4b8d;
}

.menu__toggle {
    cursor: pointer;
    margin-right: 1.8rem;
    color: rgba(2, 2, 2, 0.966);
}

.menu__toggle {
    display: none;
}

@media screen and (max-width: 800px) {
    .nav__links {
        z-index: 999;
        background: rgb(255, 255, 255);
        position: fixed;
        top: 3.5rem;
        width: 70%;
        left: -100%;
        height: 100%;
        padding: 1rem 0;
        flex-direction: column;
        color: rgb(26, 24, 24);
    }

    .nav__links .nav__items {
        margin: 1.3rem 2rem;
    }

    .menu__toggle {
        display: block;
    }
}

.show {
    left: 0;
}

.hamburger .line {
    width: 25px;
    height: 2px;
    background-color: #272a2b;
    display: block;
    margin: 5px auto;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}

.hamburger:hover {
    cursor: pointer;
}

#hamburger-11.is-active .line:nth-child(1),
#hamburger-11.is-active .line:nth-child(2),
#hamburger-11.is-active .line:nth-child(3) {
    -webkit-transition-delay: 0.2s;
    -o-transition-delay: 0.2s;
    transition-delay: 0.2s;
}

#hamburger-11.is-active .line:nth-child(2) {
    opacity: 0;
}

#hamburger-11.is-active .line:nth-child(1) {
    -webkit-transform: translateY(7px) rotate(45deg);
    -ms-transform: translateY(7px) rotate(45deg);
    -o-transform: translateY(7px) rotate(45deg);
    transform: translateY(7px) rotate(45deg);
}

#hamburger-11.is-active .line:nth-child(3) {
    -webkit-transform: translateY(-7px) rotate(-45deg);
    -ms-transform: translateY(-7px) rotate(-45deg);
    -o-transform: translateY(-7px) rotate(-45deg);
    transform: translateY(-7px) rotate(-45deg);
}