.SliderItems {
    display: flex;
    padding: 1.5rem 2.2rem;
    width: 100%;
    flex-direction: column;
}

a {
    text-decoration: none;
    color: black;
}

.slider__top {
    display: flex;
    width: 100%;
    margin-bottom: 1.5rem;
}

.slider__top .title__top {
    font-size: 1.5rem;
    font-family: 'Solway', serif;
    letter-spacing: 20px;
    color: rgba(126, 125, 125, 0.664);
}

.slider__content {
    display: flex;
}

.card__cat {
    margin: 0 1.15rem;
    width: 18rem;
}

.card__cat .image__card {
    padding: 1.5rem 0.8rem;
    background: rgb(245, 244, 244);
    border-radius: 20px;
    cursor: pointer;
    display: flex;
    align-items: flex-end;
    justify-content: center;
}

.card__cat .image__card img {
    height: 13.5rem;
    width: 13rem;
    transition: all .5s ease-in-out;
}

.card__cat .card__footer {
    font-family: 'Solway', serif;
    padding: 1.2rem 0 0 1rem;
    font-size: 1.3rem;
    letter-spacing: 1px;
}

.card__footer div {
    justify-content: space-between;
    margin-top: 0.8rem;
    display: flex;
    align-items: center;
}

.card__footer div .price {
    font-size: 1.1rem;
    letter-spacing: -1px;
}

.card__cat:hover>div .btn-purchase {
    display: block;
}

.card__cat:hover>.image__card img {
    transform: scale(1.4);
}

.card__footer div .btn-purchase {
    transition: all .5s ease-in-out;
    display: none;
    padding: 0.45rem 0.8rem;
    font-size: 1rem;
    border-radius: 8px;
    color: rgba(255, 255, 255, 0.822);
    background: #2fc0d3;
    cursor: pointer;
}

.badge {
    padding: 0.4rem 1.2rem;
    border-radius: 10px;
    position: absolute;
    letter-spacing: 2px;
    color: rgba(255, 255, 255, 0.822);
    background: rgba(214, 5, 102, 0.959);
}

.SliderItems .splide__arrow--next {
    margin-right: 2.5rem;
}

.SliderItems .splide__arrow--prev {
    left: -3.5rem;
}

@media only screen and (max-width: 600px) {

    .SliderItems {
        padding: 1rem;
    }

    .slider__top {
        padding: 0 1rem;
        width: 97%;
    }

    .slider__top .title__top {
        font-size: 1.1rem;
        letter-spacing: 7px;
    }

    .SliderItems .splide__arrow--next {
        right: 2rem;
        display: none;
    }

    .SliderItems .splide__arrow--prev {
        left: -5rem;
        display: none;
    }
}