.main__loader {
    width: 100%;
    height: 100vh;
    top: 0;
    position: fixed;
    z-index: 999999;
}

.hide__content {
    animation: zoomAnimations 1.5s ease-in-out;
    z-index: 999999;
}

@keyframes zoomAnimations {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}

.content__loader {
    width: 100%;
    height: 100%;
    background: rgb(255, 255, 255);
}

.loader {
    position: absolute;
    top: 50%;
    left: 50%;
    justify-content: center;
    align-items: center;
    display: flex;
    width: 150px;
    transform: translate(-50%, -50%);
}

.loader span {
    width: 10px;
    height: 80px;
    background: white;
    margin: 0 1px;
    animation: loading 800ms ease-in-out infinite;
}

.loader span:nth-child(1) {
    background: #0084ff;
    animation-delay: -700ms;
}

.loader span:nth-child(2) {
    background: #eb4924;
    animation-delay: -600ms;
}

.loader span:nth-child(3) {
    background: #00c300;
    animation-delay: -500ms;
}

.loader span:nth-child(4) {
    background: #ff0084;
    animation-delay: -400ms;
}

.loader span:nth-child(5) {
    background: #FFFC00;
    animation-delay: -300ms;
}

.loader span:nth-child(6) {
    background: #cd201f;
    animation-delay: -200ms;
}

@keyframes loading {

    0%,
    60%,
    100% {
        transform: scaleY(0.01);
    }

    35% {
        transform: scale(1);
    }
}