.Footer {
    width: 100%;
    /* margin-top: 3rem; */
    /* position: fixed; */
    bottom: 0;
    background: #2c3338;
    color: #d9d9d9;
    padding: 1rem 2rem;
}

.Footer .main__content {
    margin-bottom: 2.2rem;
    display: flex;
}

.Footer .left {
    width: 39%;
    margin-right: 10px;
    margin-bottom: 3rem;
}

.Footer .center {
    width: 15%;
}

.Footer .center__list {
    width: 15%;
}

.Footer .right {
    width: 22%;
}

.Footer .box h2 {
    margin-bottom: 10px;
}

.left .content p {
    margin-bottom: 15px;
}

.left .content .social__media {
    display: flex;
}

.left .content .social__media span {
    display: flex;
    background: #363b3f;
    width: 30px;
    padding: 5px;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    height: 30px;
    margin: 5px 10px;
    cursor: pointer;
    transition: .3s;
}

.left .content .social__media .fb:hover {
    background: #3b5998;
    transform: scale(1.1);
}

.left .content .social__media .twt:hover {
    background: #1da1f2;
    transform: scale(1.1);
}

.left .content .social__media .ig:hover {
    background: #da1eb1;
    transform: scale(1.1);
}

.center .content .items {
    display: flex;
    align-items: center;
    transition: .5s;
}

.center .content .items .icons__ {
    margin: 7px 10px 7px 0;
}

.center .content .items:hover {
    cursor: pointer;
    transform: scale(1.05);
    color: #00a0dc;
}

.center__list .content li {
    font-size: 1.05rem;
    margin: 10px 10px 7px 15px;
    transition: .3s;
}

.center__list .content li:hover {
    cursor: pointer;
    transform: scale(1.05);
    color: #00a0dc;
}

.right .content .form__group .label {
    margin: 5px 0;
}

.right .content .form__group input,
.right .content .form__group textarea {
    background: #363b3f;
    outline: none;
    border: none;
    padding: 8px 10px;
    width: 100%;
    color: #fcf9f9;
}

.right .content .form__group textarea {
    resize: none;
}

.btn__submit {
    margin-top: 10px;
    padding: 10px 22px;
    border: none;
    color: #fcf9f9;
    background: #35465d;
    cursor: pointer;
    transition: .5s;
}

.btn__submit .icon-send {
    margin-left: 10px;
    width: 15px;
    height: 15px;
    transform: rotate(-45deg);
    transition: .5s;
}

.btn__submit:hover {
    border-radius: 10px;
    transform: scale(1.1);
}

.Footer hr {
    opacity: 0.5;
    width: 95%;
}

.Footer .text__copyright {
    margin: 1.2rem 0 0.5rem 0;
    width: 95%;
    display: flex;
    justify-content: space-between;
}

.Footer .text__copyright span span {
    color: #1ed760;
    font-weight: 600;
}

@media screen and (max-width: 800px) {
    .Footer .main__content {
        /* flex-direction: column; */
        flex-wrap: wrap;
    }

    .Footer .left {
        width: 85%;
    }

    /* .Footer .left {
        width: 80%;
        margin-bottom: 1rem;
    } */
    /* .Footer .center {
        width: 50%;
        margin-bottom: 1rem;
    }

    .Footer .center__list {
        width: 50%;
    } */
    .left .content .social__media span {
        margin: 10px 20px 15px 10px;
    }

    .Footer .center,
    .Footer .center__list {
        display: none;
    }

    .Footer .right {
        width: 80%;
    }

    .Footer .text__copyright {
        width: 80%;
        flex-direction: column;
    }

    .Footer .text__copyright span {
        margin: 5px 0;
    }
}